<template>
  <div style="position: relative; color: white" @click="click"></div>
</template>

<script>
// import axios from 'axios'
import * as THREE from 'three'
import { LineSegments2 } from '/node_modules/three/examples/jsm/lines/LineSegments2.js'
import { LineMaterial } from '/node_modules/three/examples/jsm/lines/LineMaterial.js'
import { LineSegmentsGeometry } from '/node_modules/three/examples/jsm/lines/LineSegmentsGeometry.js'
// import { BufferGeometryUtils } from 'three/examples/jsm/utils/BufferGeometryUtils.js'
// import field from '../assets/field.json'
// import typefaceData from '../assets/fonts/fsrailway.json'
import * as D3 from 'd3'

// const D3 = Object.assign({}, require('d3-zoom'), require('d3-selection'))

// const circleSprite = new THREE.TextureLoader().load(
//   require('@/assets/sprites/disc.png')
// )

let lines = null
// let icons = null
const color = new THREE.Color()
const numberOfLines = 1000
// const numberOfIcons = 1000
// const numberOfTextSigns = 1000
const maxCoordinates = 100000

let xScale = null
let yScale = null
let xAxis = null
let yAxis = null

export default {
  name: 'GidExample',
  components: {},
  data: () => ({
    width: window.innerWidth,
    height: window.innerHeight,
    fov: 75,
    near: 300,
    far: 130000,
    scene: new THREE.Scene(),
    camera: null,
    renderer: new THREE.WebGLRenderer({ antialias: true }),
    geometry: null,
    cube: null,
    material: null,
    scale: 1,
    mouse: new THREE.Vector2(),
    raycaster: new THREE.Raycaster()
  }),
  mounted() {
    this.init()
    this.initScheme()
    this.animate()

    setInterval(async () => {
      // await this.getStates()
      this.updateLines()
      // this.updateSignals(fieldBuffer)
    }, 1000)
  },
  methods: {
    animate() {
      requestAnimationFrame(this.animate)
      this.renderer.render(this.scene, this.camera)
    },
    init() {
      xScale = D3.scaleLinear()
        .domain([0, this.width / maxCoordinates * 24])
        .range([0, this.width]);

      yScale = D3.scaleLinear()
        .domain([0, this.height])
        .range([this.height, 0]);
      
      this.camera = new THREE.PerspectiveCamera(
        this.fov,
        this.width / this.height,
        this.near - 5,
        this.far
      )
      this.scene.background = new THREE.Color(0x111111)
      this.raycaster.params.Points.threshold = 10
      this.renderer.setSize(this.width, this.height)
      this.renderer.setPixelRatio(
        window.devicePixelRatio ? window.devicePixelRatio : 1
      )
      this.$el.appendChild(this.renderer.domElement)

      window.addEventListener('resize', () => {
        this.width = window.innerWidth
        this.height = window.innerHeight
        this.camera.aspect = this.width / this.height
        this.camera.updateProjectionMatrix()
        this.renderer.setSize(this.width, this.height)
      })

      let zoom = D3.zoom()
        .scaleExtent([
          this.getScaleFromZ(this.far),
          this.getScaleFromZ(this.near)
        ])
        // .translateExtent([[-maxCoordinates, -maxCoordinates], [maxCoordinates, maxCoordinates]])
        .on('zoom', ({ transform }) => this.zoom(transform))
      let view = D3.select(this.$el)
      view.call(zoom)
      let initialScale = this.getScaleFromZ(this.far)
      let initialTransform = D3.zoomIdentity
        .translate(this.width / 2, this.height / 2)
        .scale(initialScale)
      zoom.transform(view, initialTransform)
      this.camera.position.set(0, 0, this.far)

      let SVG = D3.select(this.$el)
        .append('svg')
          .attr('width', this.width)
          .attr('height',this.height)
          .attr('style','position: absolute; top: 0')
        .append("g")

      // Add X axis
      xAxis = SVG.append("g")
        .attr("transform", `translate(0, ${this.height - 20})`)
        .call(D3.axisBottom(xScale));

      // Add Y axis
      yAxis = SVG.append("g")
        .attr("transform", `translate(100, 0)`)
        .call(D3.axisLeft(yScale));
    },
    initScheme() {
      //create lines
      const points = []
      const colors = []

      for (let i = 0; i < numberOfLines; i++) {
        color.set(Math.random() * 0xffffff)
        let r = color.r
        let g = color.g
        let b = color.b
        points.push(Math.random() * maxCoordinates, Math.random() * maxCoordinates, 1)
        points.push(Math.random() * maxCoordinates, Math.random() * maxCoordinates, 1)
        colors.push(r)
        colors.push(g)
        colors.push(b)
        colors.push(r)
        colors.push(g)
        colors.push(b)
      }

      points.push(0, 0, 1)
      points.push(0, maxCoordinates, 1)
      color.setStyle('#ffffff')
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)

      points.push(0, 0, 1)
      points.push(maxCoordinates, 0, 1)
      color.setStyle('#ffffff')
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)
      
      const linesGeometry = new LineSegmentsGeometry()
      const linesMaterial = new LineMaterial({
        vertexColors: true,
        linewidth: Math.log2(this.scale * 100) / 1.25
      })
      linesMaterial.resolution.set(window.innerWidth, window.innerHeight)
      linesGeometry.setPositions(points)
      linesGeometry.setColors(colors)
      lines = new LineSegments2(linesGeometry, linesMaterial)

      this.scene.add(lines)
    },

    updateLinesWidth() {
      if (lines) {
        lines.material.linewidth = Math.log2(this.scale * 100) * 0.7
        lines.material.needsUpdate = true
      }
    },
    updateLines() {
      const points = []
      const colors = []
      
      for (let i = 0; i < numberOfLines; i++) {
        color.set(Math.random() * 0xffffff)
        let r = color.r
        let g = color.g
        let b = color.b
        points.push(Math.random() * maxCoordinates, Math.random() * maxCoordinates, 1)
        points.push(Math.random() * maxCoordinates, Math.random() * maxCoordinates, 1)
        colors.push(r)
        colors.push(g)
        colors.push(b)
        colors.push(r)
        colors.push(g)
        colors.push(b)
      }
      
      points.push(0, 0, 1)
      points.push(0, maxCoordinates, 1)
      color.setStyle('#ffffff')
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)

      points.push(0, 0, 1)
      points.push(maxCoordinates, 0, 1)
      color.setStyle('#ffffff')
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)
      colors.push(color.r)
      colors.push(color.g)
      colors.push(color.b)

      lines.geometry.setColors(colors)
      lines.geometry.setPositions(points)
    },
    zoom(transform) {
      let scale = transform.k
      this.scale = scale
      let x = -(transform.x - this.width / 2) / scale
      let y = (transform.y - this.height / 2) / scale
      let z = this.getZFromScale(scale)
      this.camera.position.set(x, y, z)
      this.updateLinesWidth()
      
      // recover the new scale
      let newX = transform.rescaleX(xScale);
      let newY = transform.rescaleY(yScale);

      // update axes with these new boundaries
      if (xAxis && yAxis) {
        xAxis.call(D3.axisBottom(newX))
        yAxis.call(D3.axisLeft(newY))
      }
    },
    getScaleFromZ(z) {
      let halfFov = this.fov / 2
      let halfFovRadians = this.toRadians(halfFov)
      let halfFovHeight = Math.tan(halfFovRadians) * z
      let fovHeight = halfFovHeight * 2
      return this.height / fovHeight
    },
    getZFromScale(scale) {
      let halfFov = this.fov / 2
      let halfFovRadians = this.toRadians(halfFov)
      let scaleHeight = this.height / scale
      return scaleHeight / (2 * Math.tan(halfFovRadians))
    },
    toRadians(angle) {
      return angle * (Math.PI / 180)
    },
    click(event) {
      this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1
      this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1
      // this.raycaster.setFromCamera(this.mouse, this.camera)
      // let intersect = this.raycaster.intersectObject(this.signals)
      // if (intersect.length > 0) {
      //   alert(`Светофор: ${model.lights.items[intersect[0].index].name}`)
      // } else {
      //   intersect = this.raycaster.intersectObject(this.ways)
      //   if (intersect.length > 0) {
      //     alert(`Путь: ${model.id[this.sections[intersect[0].faceIndex]].name}`)
      //   }
      // }
    }
  }
}
</script>
